import React from "react";
import styled from "styled-components";

const Footer = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: #833ae1;
  @media (max-width: 320px) {
    grid-template-columns: 1fr;
  p {
    place-self: center center;
    text-transform: uppercase;
    font-size: 120%;
    color: white;
    text-align: center;
    @media (max-width: 320px) {
      display: none;
    }
  }
  button {
    place-self: center center;
    width: 200px;
    height: 50px;
    background: yellow;
    border-radius: 30px;
    font-size: 120%;
    text-transform: uppercase;
    margin: 10px;
  }
`;

export default () => (
  <Footer>
    <p>Записаться на бесплатный мастер класс</p>
    <button>Записаться</button>
  </Footer>
);
